import { useEffect, useState } from "react";
import { Input, InputValue } from "tdesign-react";
import { SearchIcon } from "tdesign-icons-react";
export type searchConfType = {label: string; key:string, value: string, callback:Function };

export function Search(props: any) {

  const data = props.searchConf;
  const [params, setParams] = useState({})
  let initValue:any = {}

  useEffect(()=> {
    data.forEach((el:searchConfType) => {
      initValue[el.key] = el.value != '' ? el.value : undefined;
    });
    setParams(initValue)
  },[])
  
  return (
    <div className="searchCont">
      {data.map((item:searchConfType, index: number) => (
        <div className="searchitem" key={index}>
          <div className="label">{item.label}</div>
          <Input
            // suffixIcon={<SearchIcon />}
            placeholder="请输入"
            value={(params as any)[item.key]}
            type="text"
            onEnter={() => {
              item.callback(params);
            }}
            onChange={(value: InputValue)=>{
              const par:any = {} 
              par[item.key] = value
              setParams({...params, ...par})
            }}
          />
          <SearchIcon className="searchIcon" name="search" size="medium" onClick={()=>{
            item.callback(params);
          }} />
        </div>
      ))}
    </div>
  );
}